@import "./modal.scss";

.nfd-help-center {
  h4,
  h3 {
    margin: 0 0 !important;
  }
  padding: 0 16px;
  .launch-help-center {
    text-align: center;
    .launch-description {
      margin: 24px 0;
    }
    .launch-action {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    button {
      width: 146px;
      height: 36px;
      background-color: #196bde;
      border: 1px solid #196bde;
      color: #ffffff;
      cursor: grab;
    }
    p {
      font-size: 18px;
    }
    a {
      margin-top: 16px;
      display: block;
      cursor: grab;
    }
  }

  .suggestions-container {
    .section-header {
      margin-bottom: 8px !important;
    }
    .video,
    .article {
      .description {
        line-height: 1.2em;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 8px 0;
      }
      &:hover {
        background: rgb(57, 172, 229, 0.1);
      }
    }
  }

  .suggestion[data-variant="video"] {
    iframe {
      height: 146px;
      width: 248px;
    }
  }

  .search-container {
    display: flex;
    justify-content: space-between;
    height: 32px;
    border: 1px solid black;
    margin: 16px 0px;
    input {
      border: none;
      &:focus {
        outline: none !important;
        box-shadow: none !important;
      }
    }
    button {
      box-shadow: none;
      border: none;
      background: transparent;
    }
  }
}

.help-container {
  width: 300px;
  height: 100vh;
  position: fixed;
  right: 0;
  background: white;
  display: block !important;
}

.wpcontent-container {
  display: flex;
  margin-right: 300px;
}

body:is(.page, .post-php, .post-new-php) {
  #wp-admin-bar-help-center {
    display: none;
  }
}

.mobile {
  #wpadminbar {
    ul#wp-admin-bar-root-default {
      li {
        width: 40px !important;
      }
    }
    li#wp-admin-bar-help-center {
      display: block;
    }
  }
}
